<template>
<el-menu-item class="mainMenuItem" :index="linkPath">
    <router-link tag="div" :to="linkPath">
        <span>{{ generateRouteTitle(route.name) }}</span>
    </router-link>
</el-menu-item>
</template>

<script>
import {
    generateRouteTitle
} from '@/util/i18n.js';
export default {
    created() {},
    props: {
        route: {
            type: Object,
        },
        baseUrl: {
            type: String,
            default: '',
        },
    },
    methods: {
        generateRouteTitle,
    },
    computed: {
        linkPath() {
            return this.baseUrl + '/' + this.route.path;
        },
    },
};
</script>

<style lang="scss" scoped>
.mainMenuItem.is-active {
    background-color: none;
}
</style>
